@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
* {
    box-sizing: border-box;
}

#teams {
  opacity: 0;
  position: relative;
  animation: fade-in 3s ease 1s forwards;
}

#banner-small {
  color: white;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 99;
  padding: 15px;
  font-size: 30px;
  font-weight: 900;
}
#banner-small > span, #banner > .p1 > span {
  font-size: 12px;
}
#banner {
  left: 45px;
  bottom: 45px;
  width: 550px;
  color: white;
  position: absolute;
  z-index: 99;
}
#banner > .p1 {
  font-weight: 900;
  font-size: 32px;
}
#banner > .p2 {
  font-size: 20px;
  font-weight: 600;
}